  text-align: center;
  letter-spacing: 1.1px;
}

h3 {
  font-size: 1.143rem;
  text-align: center;
  letter-spacing: 1.1px;
}

.a-input {
  padding: 10px;
  outline: none;
  font-family: sans-serif;
  width: 100%;
}

.a-textarea {
  height: 150px;
  resize: none;
  padding: 10px;
  font-family: sans-serif;
  outline: none;
  width: 100%;
}

.m-form {
  display: inline-flex;
  flex-direction: column;
}

.m-form__items {
  display: inline-flex;
  flex-direction: column;
}

.m-form__item, .m-form__field {
  display: inline-flex;
  flex-direction: column;
  padding-top: 15px;
  width: 350px;
}

@media (max-width: 479px) {
  .m-form__item, .m-form__field {
    width: 300px;
  }
}

.m-form__label {
  padding-bottom: 10px;
}

